<template>
  <ContentCard :title="title" @go-back="$emit('go-back')">
    <!---->
    <el-row :gutter="40">
      <el-col :span="24">
        <el-form :model="form" :rules="formRules" ref="form" label-width="160px" class="el-form-add">
          <!-- <el-form-item label="ID" prop="menuId">
            <el-input v-model="form.menuId" placeholder="ID"></el-input>
          </el-form-item>-->
          <el-form-item label="" prop="menuName">
            <el-input v-model="form.menuName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="ID" prop="elid">
            <el-input v-model="form.elid" placeholder="ID"></el-input>
          </el-form-item>
          <el-form-item label="" prop="iconClass">
            <el-input v-model="form.iconClass" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="ID" prop="tabId">
            <el-input v-model="form.tabId" placeholder="ID"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tabTitle">
            <el-input v-model="form.tabTitle" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="tabIcon">
            <el-input v-model="form.tabIcon" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="URL" prop="iframeUrl">
            <el-input v-model="form.iframeUrl" placeholder="URL"></el-input>
          </el-form-item>
          <el-form-item label="" prop="seq">
            <el-input v-model="form.seq" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="modle">
            <!-- <el-input v-model="form.modle"></el-input> -->
            <el-radio v-model="form.modle" label="0"></el-radio>
            <el-radio v-model="form.modle" label="1"></el-radio>
            <el-radio v-model="form.modle" label="3"></el-radio>
          </el-form-item>
          <el-form-item label="" prop="disabled">
            <el-radio v-model="form.disabled" label="0"></el-radio>
            <el-radio v-model="form.disabled" label="1"></el-radio>
          </el-form-item>
          <el-form-item label="ID" prop="pmid">
            <el-input v-model="form.pmid" placeholder="ID"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitBtn"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </ContentCard>
</template>
<script>
export default {
  props: {
    editId: Number,
    default: null
  },
  //  prop: ['editId'],
  data() {
    return {
      title: null,
      form: {
        // menuId: null,
        menuName: null,
        elid: null,
        iconClass: null,
        tabId: null,
        tabTitle: null,
        tabIcon: null,
        iframeUrl: null,
        seq: null,
        modle: null,
        disabled: null,
        pmid: null,
        menuId: undefined
      },
      formRules: {
        // 
        menuName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        elid: [{ required: true, message: 'ID', trigger: 'blur' }],
        iconClass: [
          { required: true, message: '', trigger: 'blur' }
        ],
        tabId: [{ required: false, message: 'ID', trigger: 'blur' }],
        tabTitle: [
          { required: true, message: '', trigger: 'blur' }
        ],
        tabIcon: [
          { required: true, message: '', trigger: 'blur' }
        ],
        iframeUrl: [
          { required: true, message: 'URL', trigger: 'blur' }
        ],
        disabled: [{ required: true, message: '', trigger: 'blur' }],
        seq: [{ required: true, message: '', trigger: 'blur' }],
        modle: [{ required: true, message: '', trigger: 'blur' }],
        pmid: [{ required: true, message: 'ID', trigger: 'blur' }]
      }
    }
  },
  methods: {
    submitBtn() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$service.saveSysMenu({ 'jsonStr': JSON.stringify(this.form) }, '').then(() => {
            this.$emit('open-list-page')
          })
        }
      })
    },
    getEditData() {
      this.$service.editSysMenu({ id: this.editId }).then(data => {
        Object.keys(this.form).forEach(v => {
          if (v === 'disabled') {
            this.form[v] = data[v] + ''
          } else {
            this.form[v] = data[v]
          }
        })
      })
    }
  },
  created() {
    if (this.editId !== null && this.editId !== undefined) {
      this.title = ''
      this.form.menuId = this.editId
      this.getEditData()
    } else {
      this.title = ''
    }
  }
}
</script>
