<template>
  <ContentCard class="content">
    <ContentWrapper
      :filter="filter"
      :filterSchema="filterSchema"
      :pagination="pagination"
      @filter-change="handleFilterChange"
      @filter-reset="handleFilterReset"
    >
     <ButtonGroupForListPage
        pageName='sysUser'
        @add="addUser"
        @edit="editData"
        @delete="batchDel"
        >
        </ButtonGroupForListPage>
      <Table
        :props="table.props"
        :header="table.header"
        :data="table.data"
        :selected="table.selected"
        :selection-type="table.selectionType"
        @row-selection-add="handleRowSelectionAdd"
        @row-selection-remove="handleRowSelectionRemove"
        @all-row-selection-change="handleAllRowSelectionChange"
      />
    </ContentWrapper>

    <!--  -->
    <el-dialog title="" :visible.sync="roleDialogVisible">
      <span>
        <el-transfer v-model="roleValue" :data="roleData" :titles="['','']" @change="handleChange"></el-transfer>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="roleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="roleDialogVisible = false;add()"> </el-button>
      </span>
    </el-dialog>
    <!-- end --->

    <!--  -->
    <el-dialog title="" width="30%" :visible.sync="dataPermissionWinVisible">
      <el-checkbox-group
      v-show="!isLoading"
      v-model="checkedDictItems">
        <el-checkbox class="checkItemStyle"
          v-for="{dictId, dictCnName} in DataPermissionItems"
          :key="dictId"
          :label="dictId">
          {{ dictCnName }}
        </el-checkbox>
      </el-checkbox-group>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dataPermissionWinVisible = false"> </el-button>
        <el-button type="primary" @click="saveProfession"> </el-button>
      </span>
    </el-dialog>
    <!-- end -->
  </ContentCard>
</template>
<script>
import _ from 'gateschema'
import ButtonGroupForListPage from '@/components/ButtonGroupForListPage'
import { ContentWrapper, Table, utils } from 'admin-toolkit'
export default {
  components: {
    Table,
    ContentWrapper,
    ButtonGroupForListPage
  },
  data () {
    return {
      depts: {}, // 
      roleData: [], // 
      roleValue: [], // 
      data1: [], // 
      user: [],
      setRolesId: [],
      roleDataSelected: null,
      roleDialogVisible: false, // 
      currentUserId: null,
      dataPermissionWinVisible: false, // 
      DataPermissionItems: {}, // 
      checkedDictItems: [], // 
      isLoading: false, // 
      selectedRole: [],
      filter: {
        sort: undefined,
        order: undefined
      },
      filterSchema: null,
      pagination: {},
      selected: [],
      table: {
        props: {},
        header: [
          {
            label: 'ID',
            prop: 'userId',
            width: '70',
            sortable: true
          },
          {
            label: '',
            'min-width': '120',
            prop: 'userName',
            sortable: true
          },
          {
            label: '',
            prop: 'loginName',
            width: '110',
            sortable: true
          },
          {
            label: '',
            prop: 'deptName',
            'min-width': '90'
          },
          {
            label: '',
            prop: 'email',
            'min-width': '160'
            // sortable: 'custom'
          },
          {
            label: 'IP',
            prop: 'loginIp',
            width: '130'
          },
          {
            label: '',
            prop: 'loginCount'
          },
          {
            label: '',
            prop: 'loginTime',
            width: '170'
          },
          {
            label: '',
            prop: 'disabled',
            formatter: function(row) {
              if (row.disabled === 0) {
                return ''
              } else if (row.disabled === 1) {
                return ''
              }
            }
          },
          {
            label: '',
            prop: 'dateCreate',
            width: '170',
            sortable: true
          },
          {
            label: '',
            prop: 'dateModify',
            width: '170',
            sortable: true
          },
          {
            label: '',
            prop: 'remark',
            'min-width': '170'
          },
          {
            label: '',
            width: '200',
            fixed: 'right',
            render: utils.component.createOperationRender(this, {
              setRole: '',
              setData: ''
            })
          }
        ],
        data: [],
        selected: [],
        selectionType: 'multiple'
      }
    }
  },
  methods: {
    /**
     * 
     */
    addUser () {
      this.$emit('open-add-page', null)
    },
    handleRead ({ row }) {},
    setRole ({ row }) {
      const object = { userId: row.userId }
      var data2 = []
      var data3 = []
      this.roleDialogVisible = true
      this.user = row.userId
      this.$service.getNotRolesByUserId(object).then(data => {
        this.data1 = data
        this.$service.getRolesByUserId(object).then(data => {
          if (data != null) {
            this.data1 = this.data1.concat(data)
          }
          for (let i = 0; i < this.data1.length; i++) {
            data2.push({
              key: this.data1[i].roleId,
              label: this.data1[i].roleName
            })
          }
          for (let j = 0; j < data.length; j++) {
            data3.push(data[j].roleId)
          }
          this.roleData = data2
          this.roleValue = data3
        })
      })
    },
    handleChange (value, direction, movedKeys) {
      var str = []
      for (var i = 0; i < value.length; i++) {
        if (str.indexOf(value[i]) == -1) {
          str.push(value[i])
        }
        // str.push(['roleIds', value[i]])
      }
      const select = str.join(',')
      this.setRolesId = select
      // this.selectedRole = this.user.concat(str)
    },
    // 
    add () {
      const userId = this.user
      const roleIds = this.setRolesId
      this.$service.saveUserRoles({ userId: userId, roleIds: roleIds }, '').then(() => {
        this.roleDialogVisible = false
        this.fetchData()
      })
    },
    editData() {
      if (this.$isAllowEdit(this.selected)) {
        this.$emit('open-add-page', this.selected[0])
      }
    },
    /**
     * 
     */
    batchDel () {
      if (this.selected.length === 0) {
        this.$message('')
        return
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$service
          .userConfigDelete({ id: this.selected.join(',') }, '')
          .then(data => {
            this.fetchData()
            this.handleAllRowSelectionRemove()
          })
      })
    },
    handleCreate () {
      this.$router.push({ name: 'prize-create' })
    },
    handleRowSelectionAdd (targetItem) {
      this.selected.push(targetItem.userId)
      this.updateTableSelected()
    },
    handleRowSelectionRemove (targetItem) {
      this.selected = this.selected.filter(item => {
        return item !== targetItem.userId
      })
      this.updateTableSelected()
    },
    handleAllRowSelectionChange (value) {
      if (value) {
        this.table.data.forEach(this.handleRowSelectionAdd)
      } else {
        this.selected = []
        this.table.selected = []
      }
    },
    handleAllRowSelectionRemove () {
      this.selected = []
      this.table.selected = []
    },
    updateTableSelected () {
      const table = this.table
      const newSelectedIndex = this.selected
      table.selected = table.data.reduce((result, item, index) => {
        if (newSelectedIndex.indexOf(item.userId) > -1) {
          result.push(index)
        }
        return result
      }, [])
    },
    handleFilterChange (type) {
      if (type === 'query') {
        if (this.pagination) {
          this.pagination.currentPage = 1
        }
      }
      this.fetchData()
    },
    handleFilterReset () {
      this.filter = {
        sort: undefined,
        order: undefined
      }
      this.pagination.currentPage = 1
      this.fetchData()
    },
    parseFilter () {
      const { filter, pagination } = this
      if (pagination) {
        filter.page = pagination.currentPage
        filter.rows = pagination.pageSize
      }
      return filter
    },
    /**
     * 
     */
    fetchData () {
      this.handleAllRowSelectionRemove()
      const filter = this.parseFilter()
      this.$service.userConfigPageList(filter).then(data => {
        this.pagination.total = data.total
        this.table.data = data.rows
      })
    },
    /**
     * 
     */
    getDepts () {
      return this.$service.getDepts().then(data => {
        data.forEach(element => {
          this.depts[element.deptName] = element.deptId
        })
      })
    },
    getNotRolesByUserId (userId) {
      this.$service.getNotRolesByUserId({ userId: userId }).then(data => {
        data.forEach(element => {
          this.depts[element.deptName] = element.deptId
        })
      })
    },
    /**
     * 
     */
    async setData ({ row }) {
      this.currentUserId = row.userId
      // businessType
      await this.userConfigBusinessType()
      // user checked id
      this.checkedDictItems = []
      this.isLoading = true
      await this.getDictCheckedByUserId(row.userId)
      this.isLoading = false
      this.dataPermissionWinVisible = true
    },
    // 
    getDictCheckedByUserId (userId) {
      return this.$service.getDictCheckedByUserId({ userId: userId }).then((data) => {
        this.checkedDictItems = data
      })

      //   return this.$service.getDictCheckedByUserId().then((data) => {
      //   this.checkedDictItems = data
      // })
    },
    // 
    userConfigBusinessType () {
      return this.$service.getDictType({ type: 'businessType' }).then(data => {
        this.DataPermissionItems = data
      })
    },
    // 
    saveProfession () {
      const dictIdGroupStr = this.checkedDictItems.join(',')
      this.$service.saveProfession({ userId: this.currentUserId, dicts: dictIdGroupStr }, '').then(() => {
        this.dataPermissionWinVisible = false
      })
    }
  },
  created () {
    let filterSchema = _.map({
      userName: _.o.string.other('form', {
        component: 'Input',
        placeholder: ''
      }),
      deptId: _.o.enum(this.depts).other('form', {
        component: 'Select',
        placeholder: ''
      }),
      loginName: _.o.string.other('form', {
        component: 'Input',
        placeholder: ''
      }),
      email: _.o.string.other('form', {
        component: 'Input',
        placeholder: ''
      }),
      loginIp: _.o.string.other('form', {
        component: 'Input',
        placeholder: 'IP'
      }),
      disabled: _.o.enum({ : '0', : '1' }).other('form', {
        component: 'Select',
        placeholder: ''
      })
    })
      .other('form', {
        cols: {
          item: 6,
          label: 0,
          wrapper: 20
        },
        layout: 'inline',
        footer: {
          cols: {
            label: 0,
            wrapper: 24
          },
          showSubmit: true,
          submitText: '',
          showReset: true,
          resetText: ''
        }
      })
    this.getDepts().then(() => {
      this.filterSchema = filterSchema
    })
    this.fetchData()
  } }
</script>
<style lang = 'stylus' scoped>
.btns
  margin-bottom 10px
.checkItemStyle
  margin 10px
</style>
