const titleMap = {
  'create': '',
  'read': '',
  'edit': '',
  'replicate': '',
  'copy': ''
}
export default {
  props: ['titlePrefix'],
  computed: {
    title() {
      const mode = this.mode
      const resourceName = this.resourceName || ''
      const title = mode === 'replicate'
        ? `${resourceName}`
        : ((titleMap[mode] || '') + resourceName)
      const titlePrefix = this.titlePrefix
      return titlePrefix ? `${titlePrefix} / ${title}` : title
    }
  }
}
