<template>
  <ContentCard class="content">
    <ContentWrapper
      :filter="filter"
      :filterSchema="filterSchema"
      :pagination="pagination"
      @filter-change="handleFilterChange"
      @filter-reset="handleFilterReset"
    >
         <ButtonGroupForListPage 
        pageName='markPanel' 
        @add="addData"
        @edit="editData"
        @delete="deleteData"
        >
        </ButtonGroupForListPage>
      <Table
        :props="table.props"
        :header="table.header"
        :data="table.data"
        :selected="table.selected"
        :selection-type="table.selectionType"
        @row-selection-add="handleRowSelectionAdd"
        @row-selection-remove="handleRowSelectionRemove"
        @all-row-selection-change="handleAllRowSelectionChange"
      />
    </ContentWrapper>
  </ContentCard>
</template>
<script>
import _ from 'gateschema'
import ButtonGroupForListPage from '@/components/ButtonGroupForListPage'
import ButtonList from './../../components/ButtonLIst'
import { ContentWrapper, Table, utils } from 'admin-toolkit'
export default {
  components: {
    Table,
    ContentWrapper,
    ButtonGroupForListPage
  },
  data() {
    return {
      businessTypes: {}, //
      pannelResources: {
        : 'o_tencent',
        : 'o_iqiyi',
        : 'o_youku'
      },
      pannelStatuses: {
        : 0,
        : 1,
        : 2,
        : 3,
        : 4,
        : 5
      },
      filter: {
      },
      filterSchema: null,
      pagination: {},
      selected: [],
      table: {
        props: {},
        header: [
          {
            label: 'ID',
            prop: 'pannelGroupId',
            width: '90'
          },
          {
            label: '',
            prop: 'pannelGroupRemark',
            width: '100',
            render: (createElement, { row }) => {
              return createElement(
                'el-button',
                {
                  attrs: {
                    type: 'text'
                  },
                  on: {
                    click: () => {
                      this.handleRead(row)
                    }
                  }
                },
                row.pannelGroupRemark
              )
            }
          },
          {
            label: '',
            prop: 'categoryName',
            width: '90'
          },
          {
            label: '',
            prop: 'pannelList',
            width: '90',
            render: (createElement, { row }) => {
              if (row.pannelList.length <0) {
                return ''
              } else {
                switch (row.pannelList[0].pannelResource) {
                  case 'o_tencent':
                    return ''
                    break
                  case 'o_iqiyi':
                    return ''
                    break
                  case 'o_voole':
                    return ''
                    break
                  case 'o_youku':
                    return ''
                    break
                }
              }
            }
          },
          {
            label: '',
            prop: 'fullTabName',
            'min-width': '90'
          },
          {
            label: '/',
            prop: 'pannelStatus',
            width: '90',
            render: (createElement, { row }) => {
              return this.$numToAuditStatus(row.pannelStatus)
            }
          },
          {
            label: '',
            prop: 'currentVersion',
            width: '70'
          },
          {
            label: '',
            prop: 'duplicateVersion',
            width: '90',
            render: (createElement, { row }) => {
              return createElement(
                'el-button',
                {
                  attrs: {
                    type: 'text'
                  },
                  on: {
                    click: () => {
                      this.handleRead(row, row.duplicateVersion)
                    }
                  }
                },
                row.duplicateVersion
              )
            }
          },
          {
            label: '',
            prop: 'lastUpdateDate'
          }
        ],
        data: [],
        selected: [],
        selectionType: 'multiple'
      }
    }
  },
  methods: {
    /** */
    fetchData() {
      this.handleAllRowSelectionRemove()
      const filter = this.parseFilter()
      this.$service.getMarkPanelList(filter).then(data => {
        console.log(data)
        this.pagination.total = data.total
        this.table.data = data.rows
      })
    },
    parseFilter() {
      const { filter, pagination } = this
      if (pagination) {
        filter.page = pagination.currentPage
        filter.rows = pagination.pageSize
        filter.idPrefix = '10'
        filter.pannelType = '8'
      }
      return filter
    },
    /** */
    addData() {
      this.$emit('create')
    },
    /** */
      editData() {
      if (this.$isAllowEdit(this.selected)) {
        this.table.data.forEach(e => {
          if (e['pannelGroupId'] === this.selected[0]) {
            if (e.pannelStatus === 2) {
              this.$emit('edit', this.selected[0])
            } else {
              this.$message({
                type: 'error',
                message: ''
              })
            }
            return
          }
        })
      }
    },
    /** */
    deleteData() {
      if (this.selected.length === 0) {
        this.$message('')
        return
      }
      if (window.confirm('')) {
        this.$service
          .deleteMarkPanel({ id: this.selected.join(',') }, '')
          .then(data => {
            this.fetchData()
          })
      }
    },
    /** */
     handleRead(row, version) {
      this.$emit('read', row.pannelGroupId, version)
    },
    /** */
    getBusinessType() {
      return this.$service.getDictType({type: 'businessType'}).then(data => {
        data.forEach(element => {
          this.businessTypes[element.dictCnName] = element.dictId
        })
        console.log(this.businessTypes)
      })
    },
    /** */
    handleRowSelectionAdd(targetItem) {
      this.selected.push(targetItem.pannelGroupId)
      this.updateTableSelected()
    },
    handleRowSelectionRemove(targetItem) {
      this.selected = this.selected.filter(item => {
        return item !== targetItem.pannelGroupId
      })
      this.updateTableSelected()
    },
    handleAllRowSelectionChange(value) {
      if (value) {
        this.table.data.forEach(this.handleRowSelectionAdd)
      } else {
        this.selected = []
        this.table.selected = []
      }
    },
    handleAllRowSelectionRemove() {
      this.selected = []
      this.table.selected = []
    },
    updateTableSelected() {
      const table = this.table
      const newSelectedIndex = this.selected
      table.selected = table.data.reduce((result, item, index) => {
        if (newSelectedIndex.indexOf(item.pannelGroupId) > -1) {
          result.push(index)
        }
        return result
      }, [])
    },
    //
   handleFilterChange(type, filter) {
     this.filter
      if(this.$validateId(this.filter.pannelGroupId)) {
        if (type === 'query') {
          if (this.pagination) {
            this.pagination.currentPage = 1
          }
        }
        this.fetchData() 
      }
    },
    //
    handleFilterReset() {
      this.filter = {
        sort: undefined,
        order: undefined
      }
      this.pagination.currentPage = 1
      this.fetchData()
    }
  },
  created() {
    let filterSchema = _.map({
      pannelGroupId: _.o.string.other('form', {
        component: 'Input',
        placeholder: 'ID'
      }),
      pannelGroupRemark: _.o.string.other('form', {
        component: 'Input',
        placeholder: ''
      }),
      categoryName: _.o.enum(this.businessTypes).other('form', {
        component: 'Select',
        placeholder: ''
      }),
      pannelResource: _.o.enum(this.pannelResources).other('form', {
        component: 'Select',
        placeholder: ''
      }),
      fullTabName: _.o.string.other('form', {
        component: 'Input',
        placeholder: ''
      }),
      pannelStatus: _.o.enum(this.pannelStatuses).other('form', {
        component: 'Select',
        placeholder: ''
      })
    }).other('form', {
        cols: {
        item: 6,
        label: 0,
        wrapper: 20
      },
      layout: 'inline',
      footer: {
        cols: {
          label: 0,
          wrapper: 24
        },
        showSubmit: true,
        submitText: '',
        showReset: true,
        resetText: ''
      }
    })
    this.fetchData()
    this.getBusinessType().then(() => {
      this.filterSchema = filterSchema
    })
  }
}
</script>

<style lang = 'stylus' scoped>
.btns {
  margin-bottom: 10px;
}
</style>
