const CATEGORY_TO_APPID = {
  '60': '4', // 
  '67': '-1',
  '61': '10', // 
  '66': '6', // 
  '31': '0' // 
}
export function getAppIDByTabCategory (key) {
  return CATEGORY_TO_APPID[key] || '-1'
}
