<template>
  <div>
    <ActionList :actions="actions" :target="this" class="actions"></ActionList>
    <el-dialog title="" :visible.sync="auditDialog" width="30%">
      <span>
        <el-form
          :model="auditForm"
          status-icon
          :rules="auditFormRule"
          ref="auditForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="auditFlag">
            <el-radio v-model="auditForm.auditFlag" label="4"></el-radio>
            <el-radio v-model="auditForm.auditFlag" label="5"></el-radio>
          </el-form-item>
          <el-form-item label="" prop="auditDesc">
            <el-input type="textarea" :rows="2" placeholder="" v-model="auditForm.auditDesc"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitAuditMessage"> </el-button>
        <el-button @click="auditDialog = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { ActionList } from 'admin-toolkit'
export default {
  components: {
    ActionList
  },
  props: {
    id: Number, //ID
    version: {
      type: String,
      default: ''
    }, //
    hasHistory: {
      type: Boolean,
      default() {
        return true
      }
    },
    type: String, //block
    status: Number, // 
    menuElId: String, //elId
    notContainBtn: {
      type: Array,
      default() {
        return []
      }
    }
  },
  watch: {
    version: function(newV, oldV) {
      this.getAuditDetailButton()
    }
  },
  data() {
    return {
      actions: {},
      auditDialog: false,
      auditForm: {
        auditFlag: '4', //
        auditDesc: '' //
      },
      auditFormRule: {
        auditDesc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitAuditMessage() {
      this.$refs.auditForm.validate(valid => {
        if (valid) {
          this.$service
            .auditTask(
              {
                id: this.id,
                version: this.version,
                type: this.type,
                auditFlag: this.auditForm.auditFlag,
                auditDesc: this.auditForm.auditDesc
              },
              this.auditForm.auditFlag==='4'? '' : ''
            )
            .then(data => {
              this.auditDialog = false
              this.$emit('auditTask-end')
            })
        }
      })
    },
    promiseAll() {
      let params = {
        resourceId: this.id,
        version: this.version,
        type: this.type,
        status: this.status,
        menuElId: this.menuElId
      }
      let p1 = this.$service.getHistoryList({ id: this.id, type: this.type }) //
      let p2 = this.$service.getAuditDetailButton(params)
      Promise.all([p1, p2]).then(result => {
        let data1 = result[0].rows
        let hasCopy = true
        data1 === null ? (data1 = []) : ''
        data1.forEach(e => {
          if (parseInt(e.status) === 2 || parseInt(e.status) === 3) {
            hasCopy = false
            return false
          }
        })
        if (!hasCopy) {
          this.notContainBtn.push('copy') //
        }
        let data2 = result[1]
        let action = {}
        data2.forEach(v => {
          if (this.notContainBtn.indexOf(v.runComm) < 0) {
            action[v.runComm] = { text: v.runName, type: 'primary' }
          }
        })
        this.actions = action
      })
    },
    getAuditDetailButton() {
      if (this.hasHistory) {
        this.promiseAll()
      } else {
        let params = {
          resourceId: this.id,
          version: this.version,
          type: this.type,
          status: this.status,
          menuElId: this.menuElId
        }
        this.$service.getAuditDetailButton(params).then(data => {
          let action = {}
          data.forEach(v => {
            if (this.notContainBtn.indexOf(v.runComm) < 0) {
              action[v.runComm] = { text: v.runName, type: 'primary' }
            }
          })
          this.actions = action
        })
      }
    },
    edit() {
      this.$emit('go-edit-Page')
    },
    delete() {
      this.$emit('delete-item')
    },
    audit() {
      //
      this.auditDialog = true
    },
    unaudit() {
      if (window.confirm('')) {
        this.$service
          .revokedAudit(
            {
              id: this.id,
              version: this.version,
              type: this.type
            },
            ''
          )
          .then(data => {
            this.auditDialog = false
            this.$emit("revokedAudit")
          })
      }
    },
    // 
    copy() {
      this.$emit('copy')
    },
    // 
    shelves() {
      let params = {
        id: this.id,
        version: this.version,
        type: this.type
      }
      this.$service.putShelves(params, '').then(() => {
        this.$emit('open-list-page')
      })
    }
  },
  created() {
    if(this.status === 5) {//
      this.notContainBtn.push('edit','delete')
    } 
    this.getAuditDetailButton()
  }
}
</script>
<style lang="stylus" scoped>
.actions
  justify-content start
</style>


