<template>
  <ContentCard :title="title" @go-back="$emit('go-back')">
    <!---->
    <el-row :gutter="40">
      <el-col :span="24">
        <el-form :model="form" :rules="formRules" ref="form" label-width="100px" class="el-form-add">
          <el-form-item label="" prop="dictCnName">
            <el-input v-model="form.dictCnName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="dictEnName">
            <el-input v-model="form.dictEnName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="dictCategory">
            <!-- <el-select  v-model="form.dictCategory"   clearable placeholder="" value-key="value">
              <el-option v-for="item in dictCategoryStr" :key="item.value" :label="item.label" :value="item">
              </el-option>
            </el-select> -->
            <el-select  v-model="form.dictCategory"   clearable placeholder="" >
              <el-option v-for="item in dictCategoryStr" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <!-- <el-button @click="cancelAdd"></el-button> -->
            <el-button type="primary" @click="submitBtn"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </ContentCard>
</template>
<script>
export default {
  props: {
    editData: {
      type: Object
    }
  },
  //  prop: ['editId'],
  data() {
    return {
      title: null,
     form: {
        dictId: null,
        dictCnName: null,
        dictEnName: null,
        dictCategory: null,
        checked: null,
        createdDate: null,
        creater: null,
        dictStatus: null,
        flag: null,
        id: null,
        keyword: null,
        lastUpdateDate: null,
        modifier: null
      },
      dictCategoryStr:[],
       formRules: {
        //
        dictCnName: [{ required: true, message: "", trigger: "blur" }],
        dictEnName: [{ required: true, message: "", trigger: "blur" }],
        dictCategory: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    /**
     * 
     */
    getDictCategoryList () {
      this.$service.getDictCategoryList().then(data => {
        //
        var obj = {}
        this.dictCategoryStr = data
      })

    },

    submitBtn() {
      this.$refs.form.validate(valid => {
        if (valid) {
          const dictObj = {
            dictId:this.form.dictId,
            dictCategory: this.form.dictCategory,
            dictCnName: this.form.dictCnName,
            dictEnName: this.form.dictEnName,
          }
          this.$service.SaveDictionary(dictObj, "").then(data => {
            this.$emit("open-list-page");
          });
        }
      });
    }
  },
  created() {
    if (this.editData !== null && this.editData !== undefined) {
      this.title = "";
      this.form = this.editData
    } else {
      this.title = "";
    }
    this.getDictCategoryList()
  }
};
</script>

