<template>
  <ContentCard :title="title" @go-back="$emit('go-back')">
    <!---->
    <el-row :gutter="40">
      <el-col :span="24">
        <el-form :model="form" :rules="formRules" ref="form" label-width="100px" class="el-form-add">
          <el-form-item label="" prop="userName">
            <el-input v-model="form.userName" placeholder="" maxlength="50"></el-input>
          </el-form-item>
          <el-form-item label="" prop="deptId">
            <el-select v-model="form.deptId" placeholder="">
              <el-option
                v-for="item in departmentList"
                :key="item.deptId+''"
                :label="item.deptName"
                :value="item.deptId"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="loginName">
            <el-input v-model="form.loginName" placeholder="" maxlength="50">></el-input>
          </el-form-item>
          <el-form-item label="" prop="loginPwd" v-if="isShowloginPwd">
            <el-input v-model="form.loginPwd" placeholder="" maxlength="50">></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input type="email" v-model="form.email" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="disabled">
            <el-radio v-model="form.disabled" label="0"></el-radio>
            <el-radio v-model="form.disabled" label="1"></el-radio>
          </el-form-item>
          <el-form-item label="" prop="remark">
            <el-input type="textarea" v-model="form.remark" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="dicts">
            <el-form-item>
              <el-checkbox-group v-model="form.dicts">
                <el-checkbox
                  v-for="item in dictEnNameList"
                  :key="item.dictId"
                  :label="item.dictId"
                >{{item.dictCnName}}</el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-form-item>
          <el-form-item>
            <!-- <el-button @click="cancelAdd"></el-button> -->
            <el-button type="primary" @click="submitBtn"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </ContentCard>
</template>
<script>
export default {
  props: {
    editId: Number,
    default: null
  },
  //  prop: ['editId'],
  data () {
    return {
      title: null,
      dictEnNameList: null,
      departmentList: [],
      isShowloginPwd: true,
      form: {
        userName: null,
        deptId: null,
        loginName: null,
        loginPwd: null,
        email: null,
        disabled: null,
        remark: null,
        superAdmin: 0,
        dicts: []
      },
      formRules: {
        // 
        userName: [
          { required: true, message: '', trigger: 'blur' },
          { max: 45, message: ' 45 ', trigger: 'blur' }
        ],
        loginName: [
          { required: true, message: '', trigger: 'blur' },
          { max: 45, message: ' 45 ', trigger: 'blur' }
        ],
        loginPwd: [{ required: true, message: '', trigger: 'blur' },
        { max: 45, message: ' 45 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    userConfigBusinessType () {
      this.$service.getDictType({type: 'businessType'}).then(data => {
        this.dictEnNameList = data
      })
    },
    submitBtn () {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$service.userConfigSave({jsonStr: JSON.stringify(this.form)}, '').then(data => {
            this.$emit('open-list-page')
          })
        }
      })
    },
    getEditData () {
      let obj = this
      this.$service.userConfigEdit({ id: this.editId }).then(data => {
        console.log(data)
        Object.keys(this.form).forEach(v => {
          if (v === 'disabled') {
            this.form[v] = data[v] + ''
          } else {
            this.form[v] = data[v]
          }
        })
      })
    },
    getDepts () {
      return this.$service.getDepts().then(data => {
        this.departmentList = data
        console.log(data)
        data.forEach((item) => { console.log(item.deptName + item.deptId) })
      })
    }
  },
  created () {
    this.userConfigBusinessType()
    this.getDepts()
    if (this.editId !== null && this.editId !== undefined) {
      this.title = ''
      this.isShowloginPwd = false
      this.form.userId = this.editId
      this.getEditData()
    } else {
      this.title = ''
    }
  }
}
</script>
