function optionsToText(options) {
  return options.reduce((result, { label, value }) => {
    result[value] = label
    return result
  }, {})
}

function optionsToEnums(options) {
  return options.reduce((result, item) => {
    result[item.label] = item.value
    return result
  }, {})
}

const status = {
  offShelves: 0,
  onShelves: 1,
  draft: 2,
  waiting: 3,
  accepted: 4,
  rejected: 5,
  auditing: 6,
  processing: 7,
  waiting2: 9
}
const statusText = {
  0: '',
  1: '',
  2: '',
  3: '',
  4: '',
  5: '',
  6: '',
  7: '',
  9: ''
}

const statusOptions = Object.keys(statusText).reduce((result, key) => {
  if (key < 6) {
    result.push({
      label: statusText[key],
      value: key
    })
  }
  return result
}, [])

const statusEnums = Object.keys(statusText).reduce((result, key) => {
  if (key < 6) {
    result[statusText[key]] = key
  }
  return result
}, {})

const sourceOptions = [
  {
    label: '',
    value: 'o_tencent'
  },
  {
    label: '',
    value: 'o_iqiyi'
  },
  {
    label: '',
    value: 'o_youku'
  }
]

const sourceEnums = optionsToEnums(sourceOptions)

const sourceOptionsWithEmpty = [
  {
    label: '',
    value: ''
  }
].concat(sourceOptions)

const sourceToPartner = {
  'o_tencent': 'tencent',
  'o_iqiyi': 'yinhe',
  'o_youku': 'youku'
}

const sourceText = {
  '': '',
  o_tencent: '',
  o_iqiyi: '',
  o_youku: ''
}

const partnerOptions = [
  {
    label: '',
    value: 'tencent'
  },
  {
    label: '',
    value: 'yinhe'
  },
  {
    label: '',
    value: 'youku'
  }
]

const panelTypeOptions = [
  {
    label: '',
    value: 'normal'
  },
  {
    label: '',
    value: 'group'
  }
]
const panelTypeText = optionsToText(panelTypeOptions)

const panelFocusOptions = [
  {
    label: '',
    value: 1
  },
  {
    label: '',
    value: 0
  }
]

const panelFocusText = optionsToText(panelFocusOptions)

const idPrefix = '10'
const idPrefixOptions = [
  {
    label: '',
    value: '10'
  },
  {
    label: '',
    value: '11'
  }
]
const idPrefixEnums = optionsToEnums(idPrefixOptions)

export default {
  status,
  statusText,
  statusOptions,
  statusEnums,
  sourceOptions,
  sourceEnums,
  sourceOptionsWithEmpty,
  sourceToPartner,
  sourceText,
  partnerOptions,
  panelFocusOptions,
  panelFocusText,
  panelTypeOptions,
  panelTypeText,
  idPrefix,
  idPrefixOptions,
  idPrefixEnums
}
