<template>
  <ContentCard :title="title" @go-back="$emit('go-back')">
    <!---->
    <el-row :gutter="40">
      <el-col :span="24">
        <el-form :model="form" :rules="formRules" ref="form" label-width="100px" class="el-form-add">
          <el-form-item label="" prop="typeName">
            <el-input v-model="form.typeName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="typePosition">
            <el-select v-model="form.typePosition" placeholder="">
              <el-option
                v-for="item in typePosition"
                :key="item.name+''"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="typePriority">
            <el-input-number v-model="form.typePriority" :min="1" label=""></el-input-number>
            <!-- <el-input v-model="form.typePriority" placeholder=""></el-input> -->
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitBtn"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </ContentCard>
</template>
<script>
export default {
  props: {
    editId: Number,
    default: null
  },
  data() {
    return {
      title: null,
      typePosition: [
        {
          name: '',
          id: 0
        },
        {
          name: '',
          id: 1
        },
        {
          name: '',
          id: 2
        },
        {
          name: '',
          id: 3
        }
      ],
      form: {
        typeId: undefined,
        typeName: null,
        typePosition: null,
        typePriority: null
      },
      formRules: {
        // 
        typeName: [
          { required: true, message: '', trigger: 'blur' },
           { max: 45, message: '45', trigger: 'blur' }
        ],
        typePosition: [
          { required: true, message: '', trigger: 'blur' }
        ],
        typePriority: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitBtn() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$service
            .globalCornerIconTypeSave({ 'jsonStr': JSON.stringify(this.form) }, '')
            .then(data => {
              this.$emit('open-list-page')
            })
        }
      })
    },
    getEditData() {
      let obj = this
      this.$service
        .globalCornerIconTypeDetail({ id: this.editId })
        .then(data => {
          Object.keys(this.form).forEach(v => {
            this.form[v] = data[v]
          })
        })
    }
  },
  created() {
    if (this.editId !== null && this.editId !== undefined) {
      this.title = ''
      this.form.typeId = this.editId
      this.getEditData()
    } else {
      this.title = ''
    }
  }
}
</script>
