<template>
  <ContentCard class="content">
    <ContentWrapper
      :filter="filter"
      :filterSchema="filterSchema"
      :pagination="pagination"
      @filter-change="handleFilterChange"
      @filter-reset="handleFilterReset"
    >
         <ButtonGroupForListPage 
        pageName='cornerIconType' 
        @add="addItem"
        @edit="editData"
        @delete="batchDel"
        @batch-audit="batchAudit"
        >
        </ButtonGroupForListPage>
      <Table
        :props="table.props"
        :header="table.header"
        :data="table.data"
        :selected="table.selected"
        :selection-type="table.selectionType"
        @row-selection-add="handleRowSelectionAdd"
        @row-selection-remove="handleRowSelectionRemove"
        @all-row-selection-change="handleAllRowSelectionChange"
      />
    </ContentWrapper>
    <!--  -->
    <el-dialog title="" :visible.sync="picDialogVisible" width="30%">
      <span class="pics"></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="picDialogVisible = false"> </el-button>
        <el-button type="primary" @click="picDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  --->
    <!--  -->
    <el-dialog title="" :visible.sync="auditDialogVisible" width="30%">
      <span>
        <el-form ref="auditForm" :model="auditForm" :rules="auditFormRules" label-width="80px">
          <el-form-item label="" prop="auditFlag">
            <el-radio v-model="auditForm.auditFlag" label="4"></el-radio>
            <el-radio v-model="auditForm.auditFlag" label="5"></el-radio>
          </el-form-item>
          <el-form-item label="" prop="auditDesc">
            <el-input type="textarea" v-model="auditForm.auditDesc"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="auditDialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitAudit"> </el-button>
      </span>
    </el-dialog>
    <!--  --->
  </ContentCard>
</template>
<script>
import _ from 'gateschema'
import ButtonGroupForListPage from '@/components/ButtonGroupForListPage'

import { ContentWrapper, Table, utils } from 'admin-toolkit'
export default {
  components: {
    Table,
    ContentWrapper,
    ButtonGroupForListPage
  },
  data() {
    return {
      materialTypes: {}, //
      typePosition: {
        //
        : 0,
        : 1,
        : 2,
        : 3
      },
       typePosition: {
        //
        : 0,
        : 1,
        : 2,
        : 3
      },
      picDialogVisible: false, //
      auditDialogVisible: false, //
      auditForm: {
        idStr: null,
        auditFlag: '4',
        auditDesc: ''
      },
      auditFormRules: {
        auditDesc: [
          { required: true, message: '', trigger: 'blur' }
        ],
        auditFlag: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      filter: {
        sort: undefined,
        order: undefined
      },
      filterSchema: null,
      pagination: {},
      selected: [],
      table: {
        props: {},
        header: [
          {
            label: 'ID',
            prop: 'typeId',
            width: '70'
          },
          {
            label: '',
            width: '120',
            prop: 'typeName',
            sortable: true
          },
          {
            label: '',
            prop: 'typePosition',
            width: '110',
            sortable: true,
            render: (createElement, { row }) => {
              return this.$changeKeyToValue(this.typePosition)[row.typePosition]
            }
          },
          {
            label: '()',
            prop: 'typePriority',
            width: '150',
            sortable: true
          },
          {
            label: '',
            prop: 'modifierName',
            width: '90'
          },
          {
            label: '',
            prop: 'createdDate',
            sortable: true
          },
          {
            label: '',
            prop: 'lastUpdateDate',
            sortable: true
          }
        ],
        data: [],
        selected: [],
        selectionType: 'multiple'
      }
    }
  },
  methods: {
    batchAudit() {
      if (this.selected.length>0) {
        this.auditDialogVisible = true
      } else {
        this.$message({
          type: 'error',
          message: ''
        })
      }
    },
    submitAudit() {
      this.$refs.auditForm.validate(valid => {
        if (valid) {
          this.auditForm.idStr = this.selected.join(',')
          this.$service
            .materialBatchAudit(this.auditForm, '')
            .then(data => {
              this.fetchData()
              this.auditDialogVisible = false
            })
        }
      })
    },
    /**
     * 
     */
    addItem() {
      this.$emit('open-add-page', null)
    },
    handleChange(value, direction, movedKeys) {
      var str = []
      for (var i = 0; i < value.length; i++) {
        str.push(['roleIds', value[i]])
      }
      this.selectedRole = this.user.concat(str)
    },
    editData() {
      if( this.$isAllowEdit(this.selected)) {
         this.$emit('open-add-page',this.selected[0])
      }
    },
    /**
     * 
     */
    batchDel() {
      if (this.selected.length === 0) {
        this.$message('')
        return
      }
      if (window.confirm('')) {
        this.$service
          .globalCornerIconTypeBatchDel(
            { id: this.selected.join(',') },
            ''
          )
          .then(data => {
            this.fetchData()
          })
      }
    },
    handleRowSelectionAdd(targetItem) {
      this.selected.push(targetItem.typeId)
      this.updateTableSelected()
    },
    handleRowSelectionRemove(targetItem) {
      this.selected = this.selected.filter(item => {
        return item !== targetItem.typeId
      })
      this.updateTableSelected()
    },
    handleAllRowSelectionChange(value) {
      if (value) {
        this.table.data.forEach(this.handleRowSelectionAdd)
      } else {
        this.selected = []
        this.table.selected = []
      }
    },
    handleAllRowSelectionRemove() {
      this.selected = []
      this.table.selected = []
    },
    updateTableSelected() {
      const table = this.table
      const newSelectedIndex = this.selected
      table.selected = table.data.reduce((result, item, index) => {
        if (newSelectedIndex.indexOf(item.typeId) > -1) {
          result.push(index)
        }
        return result
      }, [])
    },
   handleFilterChange(type, filter) {
     if (filter) { this.filter = filter}
      if(this.$validateId(this.filter.typeId)) {
        if (type === 'query') {
          if (this.pagination) {
            this.pagination.currentPage = 1
          }
        }
        this.fetchData() 
      }
    },
    handleFilterReset() {
      this.filter = {
        sort: undefined,
        order: undefined
      }
      this.pagination.currentPage = 1
      this.fetchData()
    },
    parseFilter() {
      const { filter, pagination } = this
      if (pagination) {
        filter.page = pagination.currentPage
        filter.rows = pagination.pageSize
      }
      return filter
    },
    /**
     * 
     */
    fetchData() {
      this.handleAllRowSelectionRemove()
      const filter = this.parseFilter()
      this.$service.getGlobalCornerIconTypePageList(filter).then(data => {
        this.pagination.total = data.total
        this.table.data = data.rows
      })
    }
  },
  created() {
    let filterSchema = _.map({
      typeId: _.o.string.other('form', {
        component: 'Input',
        placeholder: 'ID'
      }),
      typeName: _.o.string.other('form', {
        component: 'Input',
        placeholder: ''
      }),
      typePosition: _.o.enum(this.typePosition).other('form', {
        component: 'Select',
        placeholder: ''
      })
    }).other('form', {
        cols: {
        item: 6,
        label: 0,
        wrapper: 20
      },
      layout: 'inline',
      footer: {
        cols: {
          label: 0,
          wrapper: 24
        },
        showSubmit: true,
        submitText: '',
        showReset: true,
        resetText: ''
      }
    })
    this.filterSchema = filterSchema
    this.fetchData()
  }
}
</script>
<style lang = 'stylus' scoped>
.pics img
  max-width: 350px
  max-height: 500px
.content
  >>> table .imgs
    cursor: pointer
.btns
  margin-bottom: 10px
.checkItemStyle
  margin: 10px
</style>
