<template>
  <ContentCard class="content">
    <ContentWrapper
      :filter="filter"
      :filterSchema="filterSchema"
      :pagination="pagination"
      @filter-change="handleFilterChange"
      @filter-reset="handleFilterReset"
    >
      <div class="btns">
        <ButtonGroupForListPage 
        pageName='layout' 
        :not-contain-btns="notContainBtns"
        @add="addItem"
        @edit="editData"
        @delete="batchDel"
        >
        </ButtonGroupForListPage>
      </div>
      <Table
        :props="table.props"
        :header="table.header"
        :data="table.data"
        :selected="table.selected"
        :selection-type="table.selectionType"
        @row-selection-add="handleRowSelectionAdd"
        @row-selection-remove="handleRowSelectionRemove"
        @all-row-selection-change="handleAllRowSelectionChange"
      />
    </ContentWrapper>

  </ContentCard>
</template>
<script>
import _ from 'gateschema'
import { ContentWrapper, Table, utils } from 'admin-toolkit'
import ButtonGroupForListPage from './../../components/ButtonGroupForListPage'
export default {
  components: {
    Table,
    ContentWrapper,
    ButtonGroupForListPage
  },
  data() {
    return {
      notContainBtns: [ 'audit', 'batchAudit'],
      layoutType: {
        '6.0': 1,
        'V2': 2
      },
      layoutIsTitle: {
        : 1,
        : 0
      },
      layoutStatus: {
        : 1,
        : 0,
        : 2,
        : 3,
        : 4,
        : 5
      },
      filter: {
        sort: undefined,
        order: undefined
      },
      filterSchema: null,
      pagination: {},
      selected: [],
      selectedRows: {},
      table: {
        props: {},
        header: [
          {
            label: 'ID',
            prop: 'layoutId',
            width: '70'
          },
          {
            label: '',
            width: '200',
            prop: 'layoutName',
            sortable: true,
            render: (createElement, { row }) => {
              return createElement('el-button', {
                attrs:{
                  type: 'text'
                },
                on: {
                  click: () => {
                    this.openReview(row) 
                  }
                }
              },row.layoutName)
            }
          },
          {
            label: '',
            prop: 'layoutType',
            width: '80',
            render: (createElement, { row }) => {
              if (row.layoutType === 1) {
                return '6.0'
              } else {
                return 'V2'
              }
            }
          },
          {
            label: '',
            prop: 'layoutIsTitle',
            width: '80',
            render: (createElement, { row }) => {
              if (row.layoutIsTitle === 1) {
                return ''
              } else {
                return ''
              }
            }
          },
          {
            label: '',
            prop: 'layoutFlag',
            width: '80',
            render: (createElement, { row }) => {
              if (typeof row.layoutFlag === 'undefined') {
                return ''
              } else {
                switch(row.layoutFlag) {
                  case 0:
                    return ''
                    break
                  case 1:
                    return ''
                    break
                }
              }
            }
          },
          // {
          //   label: '',
          //   prop: 'space',
          //   width: 70,
          //   sortable:true
          // },
          // {
          //   label: '',
          //   prop: 'height',
          //    width: 70,
          //   sortable: true
          // },
          //  {
          //   label: '',
          //   prop: 'width',
          //   width: 70,
          //   sortable: true
          // },
            {
            label: '',
            prop: 'layoutStatus',
            width: 70,
            render: (createElement, {row}) => {
               let content='';
						switch(row.layoutStatus){
							case 0:
								content='';
								break;
							case 1:
								content='';
								break;
							case 2:
								content='';
								break;
							case 3:
								content='';
								break;
							case 4:
								content='';
								break;
							case 5:
								content='';
								break;
						}
						return content
            }
          },
           {
            label: '',
            prop: 'lastUpdateDate',
            sortable: true
          },
           {
            label: '',
            prop: 'modifierName',
            render :(createElement, {row}) => {
               return row.modifierName
            }
          }
        ],
        data: [],
        selected: [],
        selectionType: 'multiple'
      }
    }
  },
  methods: {
    /**
     * 
     */
    addItem() {
      this.$emit('open-add-page', {})
    },
    openReview(row) {
       this.$emit('open-review-page',row)
    },
    editData() {
      if( this.$isAllowEdit(this.selected)) {
         let row = this.selectedRows[this.selected[0]]
         if (row.layoutStatus !== 4)
         this.$emit('open-add-page',this.selectedRows[this.selected[0]])
         else {
           this.$message("")
         }
      }
    },
    /**
     * 
     */
    batchDel() {
      if (this.selected.length === 0) {
        this.$message('')
        return
      }
      if (window.confirm('')) {
        this.$service
          .getLayoutInforBatchDel(
            { id: this.selected.join(',') },
            ''
          )
          .then(data => {
            this.fetchData()
          })
      }
    },
    handleRowSelectionAdd(targetItem) {
      this.selected.push(targetItem.layoutId)
      let id = targetItem.layoutId
      this.selectedRows[id] = targetItem
      this.updateTableSelected()
    },
    handleRowSelectionRemove(targetItem) {
      this.selected = this.selected.filter(item => {
        return item !== targetItem.layoutId
      })
      delete this.selectedRows[targetItem.layoutId]
      this.updateTableSelected()
    },
    handleAllRowSelectionChange(value) {
      if (value) {
        this.table.data.forEach(this.handleRowSelectionAdd)
      } else {
        this.selected = []
        this.table.selected = []
      }
    },
    handleAllRowSelectionRemove() {
      this.selected = []
      this.table.selected = []
    },
    updateTableSelected() {
      const table = this.table
      const newSelectedIndex = this.selected
      table.selected = table.data.reduce((result, item, index) => {
        if (newSelectedIndex.indexOf(item.layoutId) > -1) {
          result.push(index)
        }
        return result
      }, [])
    },
    handleFilterChange(type, filter) {
      if (filter) { this.filter = filter}
      if (type === 'query') {
        if (this.pagination) {
          this.pagination.currentPage = 1
        }
      }
      this.fetchData()
    },
    handleFilterReset() {
      this.filter = {
        sort: undefined,
        order: undefined,
      }
      this.pagination.currentPage = 1
      this.fetchData()
    },
    parseFilter() {
      const { filter, pagination } = this
      if (pagination) {
        filter.page = pagination.currentPage
        filter.rows = pagination.pageSize
      }
      return filter
    },
    /**
     * 
     */
    fetchData() {
      this.handleAllRowSelectionRemove()
      const filter = this.parseFilter()
      this.$service.getLayoutInforPageList(filter).then(data => {
        this.pagination.total = data.total
        this.table.data = data.rows
      })
    }
  },
  created() {
    let filterSchema = _.map({
      layoutName: _.o.string.other('form', {
        component: 'Input',
        placeholder: ''
      }),
         layoutType: _.o.enum(this.layoutType).other('form', {
        component: 'Select',
        placeholder: ''
      }),
          layoutIsTitle: _.o.enum(this.layoutIsTitle).other('form', {
        component: 'Select',
        placeholder: ''
      }),
      layoutStatus: _.o.enum(this.layoutStatus).other('form', {
        component: 'Select',
        placeholder: ''
      })
    }).other('form', {
       cols: {
        item: 5,
        label: 0,
        wrapper: 20
      },
      layout: 'inline',
      footer: {
        cols: {
          label: 0,
          wrapper: 24
        },
        showSubmit: true,
        submitText: '',
        showReset: true,
        resetText: ''
      }
    })
    this.filterSchema = filterSchema
    this.fetchData()
  }
}
</script>
<style lang = 'stylus' scoped>
.pics img
  max-width: 350px
  max-height: 500px
.content
  >>> table .imgs
    cursor: pointer
.btns
  margin-bottom: 10px
.checkItemStyle
  margin: 10px
</style>
